  font-family: parisienne;
  font-size: 50px;
  text-align: center;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.contact-form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
}

.contact-form div {
  display: -webkit-flex;
  display: flex;
}

.contact-form div > input {
  display: -webkit-flex;
  display: flex;
  width: 100%;
}

.contact-form input,
textarea {
  margin: 5px 15px;
  padding: 0px 5px;
  width: 93%;
  font-size: 15px;
}

.message {
  height: 100px;
}

.contact-form button {
  margin: 8px 15px 10px;
  padding: 4px 20px;
  background-color: white;
  border: 1px solid goldenrod;
  color: goldenrod;
  font-size: 16px;
}

.contact-form button:hover {
  background-color: rgba(218, 165, 32, 0.849);
  color: white;
  border: 1px solid goldenrod;
}

@media (max-width: 1200px) {
  /* MESSAGE FORM */
  .left-side {
    padding-top: 5px;
    width: 90%;
    margin-top: 0;
    background-color: rgba(255, 255, 255, 0.884);
    border: none;
  }

  .left-side h3 {
    font-size: 40px;
  }

  .contact-form input,
  textarea {
    padding: 2px 8px;
    font-size: 12px;
    margin: 4px 10px;
  }

  .message {
    height: 100px;
  }

  .contact-form button {
    margin: 5px 0 8px;
    padding: 8px 20px;
    font-size: 13px;
    background-color: white;
    border: 1px solid goldenrod;
    color: goldenrod;
  }

  .contact-form button:hover {
     background-color: rgba(218, 165, 32, 0.849);
    color: white;
    border: 1px solid goldenrod;
  }
}

/* CONTACT INFO */

