}

.right-side h3 {
  font-family: parisienne;
  font-size: 50px;
  text-align: center;
  color: white;
}

.info-text {
  font-size: 15px;
}

.each-item-contact {
  display: -webkit-flex;
  display: flex;
  font-size: 15px;
}

@media (max-width: 1200px) {
  /* CONTACT INFO */
  .right-side {
    padding: 10px 15px;
    width: 90%;
    margin-top: 30px;
  }

  .right-side h3 {
